.section-works {
    max-width: 800px;
    margin: auto;
  }

  .card {
    display: flex;
  }

  .card div img {
    width: 246px;
    height: 180px;
    object-fit: cover;
    border-radius: 8px;
  }

  .text-works {
    font-size: 1em;
    margin-bottom: 30px;
    font-weight: 300;
  }

  .container-detail {
    margin-left: 20px;
  }

  .container-detail p {
    margin-top: 20px;
    width: 100% !important;
  }

  .label-dark {
    background-color: #142850;
    color: #fff;
    border-radius: 16px;
    padding: 5px 10px;
    font-weight: bold;
  }

  .text-light {
    color: #8695a4;
  }

  hr {
    height: 0.5px;
    background-color: #e0e0e0;
    border: 0.5px solid #e0e0e0;
    margin-top: 10px;
    margin-bottom: 20px;
  }

  .link-portafolio {
    color: #000;
    text-decoration: none;
    transition: 0.3s;
  }

  .link-portafolio:hover {
    color: #ff6464;
    text-decoration: underline;
  }