.banner {
    height: 80vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .title {
    font-size: 3em;
    font-weight: 500;
  }

  .resume {
    width: 45%;
    font-weight: 300;
    margin-top: 25px;
    margin-bottom: 25px;
  }

  .btn-primary {
    background-color: #ff6464;
    color: #fff;
    border: none;
    padding: 15px;
    text-transform: capitalize;
    font-size: 16px;
    cursor: pointer;
  }

  .img-profile {
    width: 300px;
    height: 370px;
    border-radius: 50%;
  }